import React from "react";

import Navbar from "../components/genral_components/Navbar/Navbar";

import Banner from "../templates/maincategories/maincategoriesbanner/MainCategoriesBanner";

import Footer from "../components/genral_components/Footer/Footer";

// The design and coding for the intial blog card is included in blog main.
//When the user clicks on the blog card then the detailed blog will open.
import BlogMain from "../components/blog-page-components/Blog-main/BlogMain";

import BlogPageBanner from "./BlogPageBanner.webp";

import "./Blog.css";

export default function Blog() {

  return (
    <div className="blogs-main-container">
      <Navbar />

      <Banner bannerBackgroundImage={BlogPageBanner} />

      <BlogMain />

      <Footer/>
    </div>
  );
}
