import React, { useState, useEffect } from "react";

import "./MainCategoriesBanner.css";

// import background from "./designingWebsites.jpg"

export default function MainCategoriesBanner(prop) {
  const [isMounted, setIsMounted] = useState(false);

  useEffect(() => {
    setIsMounted(true);
  }, []);

  return (
    <div>
      <div
        className={`main-categories-page-banner slide-in ${
          isMounted ? "slide-in-active" : ""
        }`}

        style={{ backgroundImage: `url(${prop.bannerBackgroundImage})` }}
      >
        <div className="main-categories-banner-content-1">
          <div>
            <h1>{prop.heading}</h1>

            <h3>
              {prop.paragraph}
            </h3>

            <div className="main-categories-banner-content-1-buttons">
              <button>{prop.button1}</button>
              <button>{prop.button2}</button>
            </div>
          </div>
        </div>

        <div className="main-categories-banner-content-2"></div>
      </div>
    </div>
  );
}
